import Charts from './components/charts.vue'; 
import Detail from './components/detail.vue'
export default {
	components:{
		Charts,
		Detail
	},
	data() {
		return {
			tabList: ["明细", "图表"],
			activeIndex: 0,
		}
	},
	onShow(){
		if(this.activeIndex === 0){
			this.activeIndex = 1;
			this.$nextTick(function(){
				this.activeIndex = 0
			})
		}else{
			this.activeIndex = 0;
			this.$nextTick(function(){
				this.activeIndex = 1
			})
		}
	},
	methods: {
		changeTab(e) {
			this.activeIndex = e;
		},
		add() {
			uni.navigateTo({
				url: './addAccount'
			})
		},
	}
}
